import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";
import vuexAlong from "vuex-along"

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    // 用户信息
    from: [],
    //     uesrnumber: "", //用户住院号
    //     uesrname: "", //用户姓名
    // 挂号信息
    dataOne: [],
    // 科室列表
    departmentsList:[]
  },
  getters: {
    uesr: state => state.from
  },
  mutations: {
    // mutations
    // 处理数据的唯一途径，state的改变或赋值只能在这里
    getdata(state, rea) {
      state.dataOne = rea
      console.log(state.dataOne);
    },
    //
    uesr(state, from) {
      state.from = from
      console.log(state.from);

    }
  },
  actions: {
    getdata(conttext) {
      axios.get("https://lc-7fHWOBLD.cn-n1.lcfile.com/7a003906730f9b104c50/json.json").then((res) => {
          let rea = res.data.data;
          // 改变ChargeDate时间格式
          rea.forEach(e => {
            if (true) {
              e.ChargeDate = new Date(e.ChargeDate)
                .toJSON()
                .replace(/T/g, " ")
                .replace(/\.[\d]{3}Z/, "");
            }
          });
          // 
          conttext.commit("getdata", rea)

        })
        .catch(error => {
          console.log("失败信息");
        });
    }
  },
  //   用vuexAlong将数据保存 刷新还在
  plugins: [vuexAlong({
    // 设置保存的集合名字，避免同站点下的多项目数据冲突
    name: "login",
    local: {
      list: ["store"],
      // 过滤模块 ma 数据， 将其他的存入 localStorage
      isFilter: true,
    },
    session: {
      // 保存模块 ma 中的 a1 到 sessionStorage
      list: ["store.from"],
    },
  })]
})
export default store



